
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
props: [],
data() {
return {
index: 0,
id:'',
shopName:'',
orderItemList:'',
order:'',
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Scroller,
Spinner,
XHeader,
CellBox,
Cell,
Divider
},
methods: {
getOrderInfo(sn){

var url = '/rest/order/orderDetail/'+sn;
var that = this;
that.axios.get(url)
.then(response => {
console.log(response);
that.shopName=response.data.data.merchantSite.site_name;
that.orderItemList=response.data.data.orderItemList;
that.order=response.data.data.order;
})
},
shop(index){
this.$router.push({name:'store',params:{'storeId':index}});
this.$router.go(0);
},
product(index){
this.$router.push({name:'productDetail',params:{pro_id:index}});
this.$router.go(0);
},

},
mounted() {

this.id= this.$route.params.id;
this.getOrderInfo(this.id);
this.showHeight = screen.availHeight - $(".white").height() - $(".vux-tab").height() - 46 + "px";

},

}



