	import md5 from 'js-md5'
    import ras from '../common/security.js'

	export default {
		props: [],
		data() {
			return {
				Tel: '',
				Pwd: '',
				modulus:'',
				exponent:'',
				loginR:true
				
			}
		},
		components: {},
		methods: {
			login() {
				var hash = md5.create();
				hash.update(this.Pwd);
				var pwd = hash.hex();
				if(this.Tel != '' && this.Pwd != '') {
//					var URL = '/rest/auth/login';
					var data = {
						'username': this.Tel,
						'password': pwd,
					}
					console.log(data);
//					var that = this;
//					that.axios.post(URL,data)
//						.then(function(){
//							alert(res.message);
//							if(window.localStorage) {
//								localStorage.setItem("isLogin", true);
//								that.$router.push({
//									path: '/home'
//								})
//							}
//						})
					data = JSON.stringify(data);
					var key = RSAUtils.getKeyPair(this.exponent, '', this.modulus);
					var login = RSAUtils.encryptedString(key, data);
					var url = '/rest/auth/login';
					var that = this;
					var data = {
						"loginInfo": login,
					};	
					
					var pathId = this.$route.query.toPath;
					that.axios.post(url, data)					
						.then(response => {		
							console.log(response);
							that.code = response.data.code;
							if(that.code == '0000') {
								var mess = JSON.stringify(response.data.data.member);
								console.log(mess);
								localStorage.setItem("myInfo", mess);
								localStorage.setItem("login", true);
								that.$router.push({
									name: 'home'
								});
							}else{
								this.$vux.alert.show({
						       		title: '',
						        	content: response.data.message,
						    	});  
							}
					})
				} else if(this.Tel == '') {
					this.$vux.alert.show({
			       		title: '',
			        	content: '请输入手机号',
			    		});  
   			
				} else {
					this.$vux.alert.show({
			       		title: '',
			        	content: '请输入密码',
			    		});  
   		
				}
			},
			changePage(str){
				if(str == 'register'){
					this.loginR = false;
				}else{
					this.loginR = true;
				}
			},//登录注册页面的切换
			close(){
				this.$router.go(-1);
			},
			getPublicKey() {
				console.log("getPublicKey");
				var url = '/rest/auth/getPublicKey';
				var that = this;
				that.axios.get(url)
					.then(response => {
						
						console.log(response);
						that.modulus = response.data.data.modulus;
						that.exponent = response.data.data.exponent;						
					})				
				},
			checkMobile(){
				var url = '/rest/userReg/checkMobileExist/'+$("#regName").val();
				var that = this;
				that.axios.get(url)
					.then(response=>{
						if(response.data.code == '0000'){
							if(response.data.data.result=='EXIST'){
								this.$vux.alert.show({
       							title: '',
        						content: '电话号码已被注册',
    							 });  
   								$("#regName").val("").focus();
							}
						}else{
							alert(response.data.message);
						}
					})
				
			},
			getVerif(){
				var url = '/rest/userReg/smsCode/'+$("#regName").val();
				var that = this;
				that.axios.get(url)
					.then(response=>{
						if(response.data.code == '0000'){
							that.validcode = response.data.data.validcode;
							window.clearInterval(this.timmer);
							var time = 60;
							this.str = time+"s";
							$(".verif-btn").text(this.str);
							that.timmer = setInterval(function(){
								if(time > 0){
									time--;
									that.str = time+"s";
									$(".verif-btn").text(that.str);
									$("#uVerif").val(that.validcode);
									window.clearInterval(this.timmer);
									$(".verif-btn").text("重新获取");
								}else{
									window.clearInterval(that.timmer);
									that.str = "再次获取";
									$(".verif-btn").text(that.str);
									$("#uVerif").val(that.validcode);
									window.clearInterval(this.timmer);
									$(".verif-btn").text("重新获取");
								}
							},1000);
						}else{
							alert(response.data.message);
						}
					})
			},
			regSubmit(){
				var regName = $("#regName").val();
				var regPwd = $("#regPwd").val();
				var rPwd = $("#rPwd").val();
				if(regPwd == rPwd){
					var hash = md5.create();
					hash.update(regPwd);
					var regPwd = hash.hex();
					var data = {
						"mobile": regName,
						"password": regPwd,
						"smsCode": this.validcode
					}
					data = JSON.stringify(data);
					
					var key = RSAUtils.getKeyPair(this.exponent, '', this.modulus);
					var reg = RSAUtils.encryptedString(key, data);
					var data = {
						'register':reg,
					}
					var url = '/rest/userReg/register';
					var that = this;
					var pathId = this.$route.query.toPath;
					
					console.log("register");
					that.axios.post(url,data)
						.then(response=>{
							console.log("res",response);
							if(response.data.code == '0000'){
								var mess = JSON.stringify(response.data.data.message);
								localStorage.setItem("message", mess);
								localStorage.setItem("login", true);
								that.$router.push({
									name: 'home'
								});
							}else{
								alert(response.data.message);
							}
						})
					
				}else{
					this.$vux.alert.show({
       					title: '',
        				content: '两次填写的密码不同，请重新填写',
    				});  
					$("#regPwd").val("");
					$("#rPwd").val("");
				}
				
			},//进行注册
		},
			mounted() {
				this.getPublicKey();
			}
	}



