
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
props: [],
components: {
Scroller,
Spinner,
XHeader,
Loading

},
data() {
return {
showHeight: "",
status2: {
pulldownStatus: 'default'
},
num: '',
info: [],
}
},
methods: {

setData: function (id) {
console.log(id);
},
setDefault: function (e, index) {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
var addr_id = that.info[index].addr_id;
var url = '/rest/addressList/default';
that.axios.post(url, { 'id': addr_id })
.then(response => {
that.getAddressList();
})


},
addDelete: function (index) {
this.num = index;
var bh = $(".address-page").height();
var bw = $(".address-page").width();
$('#fullbg').css({
height: bh,
width: bw,
display: "block"
});
$(".shadow").show();
},

addCancel: function () {
$(".shadow").hide();
},
willDelete: function () {
$(".shadow").hide();
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
var addr_id = that.info[that.num].addr_id;
var url = '/rest/addressList/' + addr_id;
that.axios.delete(url)
.then(response => {
that.getAddressList();

})

},
load3() {
setTimeout(() => {
this.$refs.scroller.donePulldown()
}, 2000)
},
getAddressList() {
var url = '/rest/addressList/getAddressList';
var that = this;
that.axios.get(url)
.then(response => {
that.info = response.data.data.addressList;
that.$nextTick(() => {
that.$refs.scroller.reset();

})
that.$vux.loading.hide();

})
}

},


mounted() {
this.showHeight = document.documentElement.clientHeight - 96 + 'px';
this.getAddressList();
}
}



