
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
components: {
Group,
XInput,
XTextarea,
XAddress,
ChinaAddressV3Data,
XHeader,
Toast,
XButton,
Loading
},
data() {
return {
id: '',
urName: '',
urPhone: '',
title: '',
text: '',
titleName: '',
valueAdd: [],
province: '', city: '', zone: '',
provinceId: '', cityId: '', zoneId: '',
addressData: [],
urAdd: '',
parameters: '',
position: 'default',
showPositionValue: false
}
},

methods: {
/**** 城市选择器消失时调用  *****/
logHide(str) {

if (str) {


var arrayString = $(".vux-popup-picker-value").text().split(" ");
var arrayId = this.valueAdd;
console.log(arrayId, arrayString);

this.province = arrayString[0];
this.city = arrayString[1];
this.zone = arrayString[2];
this.provinceId = arrayId[0];
this.cityId = arrayId[1];
this.zoneId = arrayId[2];

} else {

}
},
logShow(str) {

},
goBack() {
this.$router.go(-1);
},
showPosition(position) {
this.position = position
this.showPositionValue = true
},
save(position) {
var nameL = $(".urName").val().length;
var phoneL = $(".urPhone").val().length;
var urAddL = $("#fromAddrInfo").val().length;

if (nameL == 0 || phoneL != 11 || nameL == 0) {
this.text = "请正确填写信息";
this.showPosition(position);
console.log(this.parameters);
} else {
if(this.id == 0){
this.addAddress(position);

}else{
this.editAddress(position);

}



}
},
editAddress(position) {
var arrayString = $(".vux-popup-picker-value").text().split(" ");
var arrayId = this.valueAdd;
this.province = arrayString[0];
this.city = arrayString[1];
this.zone = arrayString[2];
this.provinceId = arrayId[0];
this.cityId = arrayId[1];
this.zoneId = arrayId[2];
var parameters = {
'addr_id':this.id,
'name': this.urName,
'mobile': this.urPhone,
'province_id': this.provinceId,
'province': this.province,
'city_id': this.cityId,
'city': this.city,
'region_id': this.zoneId,
'region': this.zone,
'addr': this.urAdd,
'def_addr': 0,
};
var that = this;
var url = '/rest/addressList/updateAddr';
that.axios.post(url,parameters)
.then(response => {
console.log(response);
if (response.data.code == '0000') {
that.text = "编辑成功";
that.showPosition(position);
that.goBack();
}

})



},
addAddress(position) {

var arrayString = $(".vux-popup-picker-value").text().split(" ");
var arrayId = this.valueAdd;
this.province = arrayString[0];
this.city = arrayString[1];
this.zone = arrayString[2];
this.provinceId = arrayId[0];
this.cityId = arrayId[1];
this.zoneId = arrayId[2];
var parameters = {
'name': this.urName,
'mobile': this.urPhone,
'province_id': this.provinceId,
'province': this.province,
'city_id': this.cityId,
'city': this.city,
'region_id': this.zoneId,
'region': this.zone,
'addr': this.urAdd,
'def_addr': 0,
};
var that = this;
var url = '/rest/addressList/addAddress';
that.axios.post(url,parameters)
.then(response => {
console.log(response);
if (response.data.code == '0000') {
that.text = "添加成功";
that.showPosition(position);
that.goBack();
}

})



},

judgeName: function () {
var that = this;
var nameL = $(".urName").val().length;
if (nameL >= 10 || nameL == 0) {
this.$vux.alert.show({
title: '',
content: '请输入正确格式的名字',
});
}
},

judgePhone: function () {
var that = this;
var phoneL = $(".urPhone").val().length;
if (phoneL != 11) {
this.$vux.alert.show({
title: '',
content: '请输入正确格式的手机号',
});
}
},

getRegions: function () {
var url = '/rest/regions';
var that = this;
that.axios.get(url)
.then(response => {
var regions = response.data.data.regions;
for (var i = 0, len = regions.length; i < len; i++) {
that.addressData.push({ 'name': regions[i].local_name, 'parent': regions[i].p_region_id.toString(), 'value': regions[i].region_id.toString() });

}

if (that.id == 0) {
that.valueAdd = ["110000", '110100', '110101'];
that.$vux.loading.hide()


} else {
var url = '/rest/addressList/' + that.id;
that.axios.get(url)
.then(response => {
console.log(response);
var addresInfor = response.data.data.address;
that.urName = addresInfor.name;
that.urPhone = addresInfor.mobile;
that.urAdd = addresInfor.addr;
that.valueAdd = [addresInfor.province_id.toString(), addresInfor.city_id.toString(), addresInfor.region_id.toString()];
that.$vux.loading.hide()

})

}

})
}

},
mounted() {
this.$vux.loading.show({
text: 'Loading'
})
this.id = this.$route.params.add_id;
if (this.id == 0) {
this.titleName = '添加地址';

} else {
this.titleName = '编辑地址';

}
this.getRegions();

},
}



