
import foot from '../../components/footer'
import Blur from 'vux/src/components/blur/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
props: [],
data() {
return {
name: '',
orderIndex: [],
url: 'https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg',
headPic: '',
backPic: 'http://i4.bvimg.com/1949/abed21e62c7dfcab.png'
}
},
components: {
Blur,
Card,
XHeader,
foot,
Loading
},
methods: {
Request() {
var that = this;
that.$vux.loading.show({
text: 'Loading'
});
var url = '/rest/my/getMyInfo';
that.axios.get(url)
.then(response => {
that.$vux.loading.hide();

if (response.data.code == '0000') {
var message = response.data.data.member;
that.name = message.nickname;
if (message.faceShow) {
that.headPic = message.faceShow;
} else {
that.headPic = "https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg";
} //如果头像存在，显示，不存在，显示本地头像
var mess = JSON.stringify(response.data.data.member);
localStorage.setItem("myInfo", mess);
that.getUserOrderIndex();
} else {
that.logOut();

}

});
},
getUserOrderIndex() {
var that = this;
var url = '/rest/order/getMyIndexTop';
that.axios.get(url)
.then(response => {
that.$vux.loading.hide();
var indexs = response.data.data;
that.orderIndex = [indexs.dfk, indexs.dfh, indexs.yfh, indexs.thh, indexs.ywc];
});

},
logOut() {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
var url = '/rest/logOut';
that.axios.get(url)
.then(response => {
that.$vux.loading.hide();
localStorage.removeItem('myInfo');
localStorage.setItem('login', false);
that.$router.push({
path: '/m/home'
});
})
},
},
mounted() {

var that = this;

this.active("#myself");
this.Request();

}
}



