			$("#add").click(function() {
				console.log(that.quantity);
				that.quantity = a + 1;
				//b=a+1;
				//t.val(b);  
				//$("#min").removeAttr("disabled");
				//setTotal();
			})
			$("#min").click(function() {
				that.quantity = a - 1;
				//b=a-1
				if(that.quantity < 0)
					that.quantity = 0;
				//t.val(b);

				//setTotal();

			})

			function setTotal() {
				//	$("#select-color").html(color.text());
				//	$("#total").html((quantity*price).toFixed(2));
			}
			//	setTotal();
		},

		Request() {
			var url = '/rest/feature/featureDetail';
			var that = this;
			var data = {
				brandId: that.brandId
			}
			that.axios.post(url, data).then(response => {
				that.mcontent = response.data.data.brand.mcontent;

			})
		},

		mounted() {
			var banner = new Swiper('.banner', {
				pagination: '.swiper-pagination',
				paginationClickable: true,
				loop: true,
				speed: 600,
				autoplay: 4000,
				onTouchEnd: function() {
					banner.startAutoplay()
				}
			});
		}
	}
}



