
import headtitle from '../components/header'
import foot from '../components/footer'
import youlike from './youlike'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
props: [],
data() {
return {
hasGoods:'',
goodsList: '',
disabled: false,
selectArr: [],
quantity:0,
totalPrice:0.0,
newnum:0,
itemPrice:0,
shopList:'',
shopIndex:'',
list1: [],
value1_1: ['iPhone'],
info:[],
name:'gintoki',
mobile:'18804259057',
address:'kkkkkkkkkkkkkk',
test:'快递￥12.00',
hasAdd:false,
shipAddrId:'',
merchantIds:'',
showHeight:''

}
},
components: {
headtitle,
youlike,
foot,
GroupTitle,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XButton,
XHeader,
Cell,
Group,
PopupPicker,
Picker,
XInput,
CellBox,
Divider,
Loading,
Scroller
},
methods: {
product(index){
this.$router.push({name:'productDetail',params:{pro_id:index}});
this.$router.go(0);
},
onShow(){
var url = '/rest/addressList/getAddressList';
var aaa
var that = this;
that.axios.get(url)
.then(response => {
this.$nextTick(() => {
var length;
var data;
var name,mobile,province,city,region,addr,id;
var item;
var aaa=[];
data=response.data.data.addressList;
length =  response.data.data.addressList.length;
for(var i=0;i<length;i++){
name=data[i].name;
mobile=data[i].mobile;
province=data[i].province;
city=data[i].city;
region=data[i].region;
addr=data[i].addr;
id=data[i].addr_id;
console.log("shipAddrId-show",id);
item=name+" "+mobile+" "+province+" "+city+" "+region+" "+addr+" "+id;
aaa[i]=item;
console.log("shipAddrId-item",item);

}
that.list1 = [aaa];
})

})
},
onChange (index) {
var data=index[0];
var data2=[];
data2=data.split(" ");
this.name=data2[0];
this.mobile=data2[1];
this.address=data2[2]+data2[3]+data2[4]+data2[5];
this.shipAddrId=data2[6];
console.log("shipAddrId-get",this.shipAddrId);
},

carList(id) {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
var url = "/rest/cart/goBuy/"+id;

var count;
that.axios.get(url)
.then(response => {
console.log("cl",response);
that.shopList = response.data.data.checkoutList;
var i;
for(i=0;i<that.shopList.length;i++){
that.totalPrice+=that.shopList[i].merchant.shopOrderPrice.originalPrice;
that.merchantIds+=that.shopList[i].merchant.merchant_id+",";
}



that.$vux.loading.hide();
})

this.getDefaultAdd();
},
getDefaultAdd(){
var url = '/rest/addressList/getAddressList';
var that = this;
that.axios.get(url)
.then(response => {
this.$nextTick(() => {
var data;
var length;
data=response.data.data.addressList;
length =  response.data.data.addressList.length;
if(length>=1)
that.hasAdd=true;
else
that.hasAdd=false;

that.shipAddrId=data[0].addr_id;
console.log("shipAddrId-get",data[0].addr_id);
that.name=data[0].name;
that.mobile=data[0].mobile;
that.address=data[0].mobile+data[0].province+data[0].city+data[0].region+data[0].addr;
})

})
},
commitOrder(){

var that=this;
var url = "rest/order/commitOrder";

var data = {
merchantIds: this.merchantIds,
shipAddrId:this.shipAddrId,

}
console.log("shipAddrId",data.shipAddrId);
console.log("merchantIds",data.merchantIds);
that.axios.post(url, data).then(response => {
console.log("commitOrder",response);
})
}

},

mounted() {

var that = this;
this.active("#cart");
var id= this.$route.params.ids;
this.carList(id);
}
}



