
import headtitle from "../components/head"
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
data() {
return {
typeId: '',
cat: '',
goodsSelector: '',

sorter: true,
sorttype: 'desc',
webpage: '',
result: '',
num: 0,
show: false,
goodsInfo: '',
counte: 1,
productList: '',
chooseProduct: '',

catId: '',
pageNo: 2,
pageSize: 3,

sorter_sale: false,
sorter_price: false,
sorter_time: false,
index_s: 0,
keyWord: '',
quantity: '',
catName: '',
showHeight: ''



}
},
components: {
headtitle,
Loading,
XHeader,
Scroller
},
methods: {
getGoodsList(id) {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
var url = '/rest/goods/getGoodsByCatId';
var data = {
catId: id,
pageNo: 1,
pageSize: 50
}

that.axios.post(url, data)
.then(response => {
that.catName = response.data.data.catName;
that.webpage = response.data.data.page;
that.result = (response.data.data.page.result.length) > 0;
that.$nextTick(() => {
that.$refs.scroller.reset();
});
that.$vux.loading.hide();

})
},
Request(index_sort) {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
var url = "/rest/goodsSearch/getList";

var data = {
sort: index_sort,
catId: that.catId,
keywords: that.keyWord
}
if (index_sort == 0)
that.getGoodsList(that.catId);


else {
that.axios.post(url, data).then(response => {

that.webpage = response.data.data.page;
that.result = (response.data.data.page.result.length) > 0;
that.$vux.loading.hide();

})
}

that.sorter_sale = false;
that.sorter_price = false;
that.sorter_time = false;
that.sorter = true;
},


changeOrder(val) {
var that = this;
switch (val) {
case 'sales':

if (that.sorttype == 'desc') {
that.sorttype = 'asc';
that.index_s = 7;
}
else {
that.sorttype = 'desc';
that.index_s = 6;
}
that.Request(that.index_s);
that.sorter_sale = true;
that.sorter_price = false;
that.sorter_time = false;
that.sorter = false;
break;
case 'price':


if (that.sorttype == 'desc') {
that.sorttype = 'asc';
that.index_s = 3;
}
else {
that.sorttype = 'desc';
that.index_s = 2;
}
that.Request(that.index_s);
that.sorter_sale = false;
that.sorter_price = true;
that.sorter_time = false;
that.sorter = false;
break;
case 'time':
if (that.sorttype == 'desc') {
that.sorttype = 'asc';
that.index_s = 9;
}
else {
that.sorttype = 'desc';
that.index_s = 8;
}
that.Request();
that.sorter_sale = false;
that.sorter_price = false;
that.sorter_time = true;
that.sorter = false;
break;
}
},


showCar(goodsId) {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
this.show = true;
var url = "/rest/goods/" + goodsId;


that.axios.get(url)
.then(response => {
that.goodsInfo = response.data.data;
that.productList = response.data.data.productList;
that.chooseProduct = that.productList[that.productList.length - 1];
console.log(that.productList);
console.log("ccc",that.chooseProduct);
that.$nextTick(() => {
that.$refs.scroller.reset();
});
that.$vux.loading.hide();
})
},


changeClass(event, className) {
var this_ = event.currentTarget;
$(this_).addClass(className).siblings().removeClass(className);


var val = $(this_).attr("value");
for (var i = 0; i < this.productList.length; i++) {
var specList = this.productList[i].specList;
for (var j = 0; j < specList.length; j++) {
if (specList[j].spec_value_id == val) {
this.chooseProduct = this.productList[i];
break;
}
}
}


},


addCar(product_id) {
var that = this;
that.$vux.loading.show({
text: 'Loading'
})
this.show = false;
var url = "/rest/cart/add";

var data = {
productId: product_id,
num: that.counte,
}
that.axios.post(url, data).then(response => {

this.$vux.alert.show({
title: '',
content: '加入购物车成功',
});
that.$vux.loading.hide();
})
},


},
mounted() {
this.showHeight = document.documentElement.clientHeight - 46 + 'px';
var that = this;
this.active("#category");
that.catId = this.$route.params.catId;
that.keyWord = this.$route.params.keyWord;

this.Request(10);
this.getGoodsList(that.catId);



},
watch: {
goodsInfo: function () {
this.$nextTick(function () {
$('.sp').each(function () {
$(this).find('span').eq(0).addClass("act");
});
})
}
}
}



