import jquery from '../../static/js/jquery-1.8.1.js'
export default {
  methods: {

  },
  mounted() {
    $(".foorer-nav span").each(function () {
      $(this).click(function () {
        $(this).siblings().removeClass("active");
        $(this).addClass("active");
      });
    });
  }

}



