
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
props: [],
data() {
return {
footPrint: [],
showHeight: '',
}
},
components: {
XHeader,
Scroller,
Loading
},
methods: {
footprint() {
var that=this;
that.$vux.loading.show({
text: 'Loading'
})
var url = '/rest/visited/getMyVisitedList';
that.axios.get(url).then(response => {

that.footPrint = response.data.data.visitedList;
that.$nextTick(() => {
that.$refs.scrollerEvent.reset()
})
that.$vux.loading.hide();
})

},

add(index) {

this.$router.push({
name: 'productDetail',
params: {
pro_id: index
}
});

}
},
mounted() {
this.showHeight = document.documentElement.clientHeight - 46 + 'px';
this.footprint();
}
}



