export default {
	props: [],
	data() {
		
		return {
			id:'',
			quantity:'',
			nearby: ['手机', '电脑', '移动电源', '零食', '饰品', '夏季新品凉鞋', '可口可乐', '鹿晗'],
			hot: ['手机', '电脑', '移动电源', '零食', '饰品', '夏季新品凉鞋', '可口可乐', '鹿晗'],
		}
	},
	components: {

	},
	methods: {
		back(){
			this.$router.go(-1);
		},
		search(){
			this.$router.push({ name: 'allStoreGoods', params: {id:this.id,keyword:this.quantity} });
		}
	},
	mounted() {
		this.id = this.$route.params.id;
		console.log("id",this.id);
	}
}



