
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
props: [],
data() {
return {
index: 0,
tabList: ["可使用", "不可使用"],
demo2: "可使用",
couponList: [1, 2, 3, 4],
showHeight: "",
status2: {
pulldownStatus: 'default'
}
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Scroller,
Spinner,
XHeader
},
methods: {
load3() {
setTimeout(() => {
this.$refs.myScroller.donePulldown()
}, 2000)
},
onClickTab(index) {
switch (index) {
case 0:
this.demo2 = '可使用';
this.couponList = [1, 2, 3, 4];
break;
case 1:
this.demo2 = '不可使用';
this.couponList = [3, 4];
break;

}
let _this = this;
_this.$nextTick(() => {
_this.$refs.myScroller.reset({
top: 0
})
})
}

},
mounted() {
this.active("#myself");
this.showHeight = screen.availHeight - $(".white").height() - $(".vux-tab").height() - 50 + "px";
}
}



