
import headtitle from "../components/head"
import Loading from 'vux/src/components/loading/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
data() {
return {
typeId: '',
cat: '',
goodsSelector: '',

sorter:true,
sorttype:'desc',
webpage: '',
result: '',
num: 0,
show: false,
goodsInfo: '',
counte: 1,
productList: '',
chooseProduct: '',

catId:'',
pageNo:2,
pageSize:3,

sorter_sale:false,
sorter_price:false,
sorter_time:false,
index_s:0,
keyWord:'',
quantity:'',
showHeight:'',
id:''
}
},
components: {
headtitle,
Loading,
XHeader,
Scroller
},
methods: {
back(){
this.$router.go(-1);
},
search(){
this.$router.push({
name:'category'
});
},

refresh(keyword){
var that=this;
that.$vux.loading.show({
text: 'Loading'
})
var url = "/rest/goodsSearch/getList";
var data = {
keywords:keyword,
siteId:that.id,
}

that.axios.post(url, data).then(response => {
that.webpage = response.data.data.page;
that.result = (response.data.data.page.result.length) > 0;
that.$vux.loading.hide();
})
that.sorter_sale=false;
that.sorter_price=false;
that.sorter_time=false;
that.sorter=true;

},
shortGoods(index_sort){
console.log(index_sort)
var that=this;




var url = "/rest/goodsSearch/getList";
var data = {
sort: index_sort,
siteId:that.id
}
if(index_sort==0)
that.getGoodsList(that.catId);
else{
that.axios.post(url, data).then(response => {
console.log(response);
that.webpage = response.data.data.page;
})
}

},
changeOrder(val) {
var that = this;
switch(val) {
case 'sales':

if(that.sorttype=='desc'){
that.sorttype='asc';
that.index_s=7;
}
else{
that.sorttype='desc';
that.index_s=6;
}
that.shortGoods(that.index_s);
that.sorter_sale=true;
that.sorter_price=false;
that.sorter_time=false;
that.sorter=false;
break;
case 'price':

if(that.sorttype=='desc'){
that.sorttype='asc';
that.index_s=3;
}
else{
that.sorttype='desc';
that.index_s=2;
}
that.shortGoods(that.index_s);
that.sorter_sale=false;
that.sorter_price=true;
that.sorter_time=false;
that.sorter=false;
break;
case 'time':
if(that.sorttype=='desc'){
that.sorttype='asc';
that.index_s=9;
}
else{
that.sorttype='desc';
that.index_s=8;
}
that.shortGoods(that.index_s);
that.sorter_sale=false;
that.sorter_price=false;
that.sorter_time=true;
that.sorter=false;
break;
}
},


showCar(goodsId) {
var that=this;
that.$vux.loading.show({
text: 'Loading'
})
this.show = true;
var url = "/rest/goods/" + goodsId;


that.axios.get(url)
.then(response => {
that.goodsInfo = response.data.data;
that.productList = response.data.data.productList;
that.chooseProduct = that.productList[that.productList.length - 1];

that.$vux.loading.hide();
})
},


changeClass(event, className) {
var this_ = event.currentTarget;
$(this_).addClass(className).siblings().removeClass(className);


var val = $(this_).attr("value");
for(var i = 0; i < this.productList.length; i++) {
var specList = this.productList[i].specList;
for(var j = 0; j < specList.length; j++) {
if(specList[j].spec_value_id == val) {
this.chooseProduct = this.productList[i];
break;
}
}
}


},


addCar(product_id) {
var that=this;
that.$vux.loading.show({
text: 'Loading'
})
this.show = false;
var url = "/rest/cart/add";

var data = {
productId: product_id,
num: that.counte,
}
that.axios.post(url, data).then(response => {

this.$vux.alert.show({
title: '',
content: '加入购物车成功',
});
that.$vux.loading.hide();
})
},


},
mounted() {

this.id = this.$route.params.id;
this.keyWord=this.$route.params.keyword;
console.log("idddd",this.id);
this.refresh(this.keyWord);
},

}



