
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {
props: [],
data() {
return {
shopId:'',
index:0,
date11: '',
turn:false,
tabList: ['全部', '待付款', '待发货', '待收货', '退换货', '已完成'],
ordList: [],
demo2: '全部',
showHeight: "",
result: '',
status2: {
pulldownStatus: 'default'
}
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Scroller,
Spinner,
XHeader,
Loading
},
methods: {
onScroll(pos) {


},
load3() {
setTimeout(() => {
this.$refs.scroller.donePulldown()
}, 2000)
},
shop(index){
this.$router.push({name:'store',params:{'storeId':index}});
this.$router.go(0);
},





onClickTab(index) {

this.$vux.loading.show({
text: 'Loading'
})
switch (index) {
case 0:
this.demo2 = '全部';
this.getOrderType();
break;
case 1:
this.demo2 = '待付款';
this.getOrderType(0);
break;
case 2:
this.demo2 = '待发货';
this.getOrderType(4);
break;
case 3:
this.demo2 = '待收货';
this.getOrderType(5);
break;
case 4:
this.demo2 = '退换货';
this.ordList = [11, 2, 3, 4, 1, 2, 3];
this.$vux.loading.hide();

break;
case 5:
this.demo2 = '已完成';
this.getOrderType(7);
break;

}
},
getOrderType(id) {
var that = this;
var url = "rest/order/getMyOrder";

var data = {
status: id
}
that.axios.post(url, data).then(response => {

that.result = response.data.data.result;
that.$nextTick(() => {
that.$refs.scroller.reset({
top: 0
});
});
that.$vux.loading.hide();

})
},
orderInfo(index){
this.$router.push({ name: 'orderInfo', params: {id: index } });
this.$router.go(0);
}
},
mounted() {
this.active("#myself");

this.index = Number(this.$route.params.index);
this.onClickTab(this.index);

this.showHeight = screen.availHeight - $(".white").height() - $(".vux-tab").height() - 46 + "px";

},
updated() {

}
}



