
import headtitle from '../components/header'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
props: [],
data() {
return {
hasGoods:'',
goodsList: '',
disabled: false,
selectArr: [],
quantity:0,
totalPrice:0.0,
newnum:0,
testTitle:['春','夏'],
num:0,
list2:['春','夏'],
demo2:'春',
index: 0,
id:'',
feature:''


}
},
components: {
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem,
Scroller,
XHeader
},
methods: {
change(e,index){
this.num=index;
},
onScroll (pos) {
this.scrollTop = pos.top
},
getThemeInfo(id){
var that=this;
that.$vux.loading.show({
text: 'Loading'
})
this.show = true;
var url = "/rest/feature/featureDetail/" + id;

that.axios.get(url)
.then(response => {

that.feature = response.data.data.feature.mimgShow;

that.goodsList=response.data.data.goodsList;
that.$vux.loading.hide();
})
},
add(index) {

this.$router.push({ name: 'productDetail', params: { pro_id: index } });
this.$router.go(0);

}


},
mounted() {
var that = this;
that.id = this.$route.params.id;
this.getThemeInfo(that.id);


}
}



